import React, { useState, useEffect } from "react";

const Notice = () => {
  const [data, setData] = useState();
  const getData = async () => {
    try {
      const res = await fetch(
        "https://sheet.best/api/sheets/cd012735-aa03-4afe-b86f-4f38cd11a3b7"
      );
      const data = await res.json();
      setData(data);
    } catch (error) {
      console.log("error");
    }
  };
  useEffect(() => {
    getData();
  }, []);
  const mystyle = {
    backgroundColor: "#00A300",
    color: "white",
    padding: "7px",
    border: "none",
    borderRadius: "3px",
    outline: "none",
    textDecoration: "none!important",
  };

  return (
    <>
      <div
        className="container text-justify"
        style={{
          backgroundColor: "#fff",

          minHeight: "86vh",
        }}
      >
        <div
          className="h1 display-1 font-weight-bold text-success "
          style={{ paddingTop: "100px" }}
        >
          Notice
        </div>
        <div className="p-2  ">
          {data
            ?.slice(0)
            .reverse()
            .map((item, i) => (
              <>
                {" "}
                <div class="jumbotron shadow-lg bg-white">
                  <h1 class="display-4">{item.title}</h1>
                  <p class="lead">{item.date}</p>
                  <hr class="my-4" />
                  <p style={{ fontSize: "10px" }}>{item.description}</p>
                  <a
                    style={{ fontSize: "14px" }}
                    class="btn btn-light text-white"
                    style={mystyle}
                    target="_blank"
                    href={item.link}
                    role="button"
                  >
                    Link
                  </a>
                </div>
              </>
            ))}
        </div>
      </div>
    </>
  );
};

export default Notice;
