import React, { Component } from "react";

import { ViewButton } from "./ActivitiesElements";
import background from "../../img/Activities.jpg";

export class Activities extends Component {
  render() {
    return (
      <>
        <div
          className="container-fluid text-center"
          style={{
            backgroundImage: ` linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) ),url(${background})`,
            backgroundPosition: `contain`,
            backgroundRepeat: `no-repeat`,
            backgroundSize: `100vw 100vh`,
          }}
        >
          <div
            class="jumbotron bg-transparent text-white"
            style={{ zIndex: `10` }}
          >
            <h1 class="display-1 font-weight-bold p-5 ">Student Activities</h1>
          </div>
        </div>
        <div
          className="container-fluid text-center"
          style={{ backgroundColor: "#fff" }}
        >
          <div class="jumbotron bg-white">
            <p class="container">
              Students are the life and soul of all educational enterprise. At
              the CIT Kokrajhar, Students have the unique opportunity of not
              just witnessing an Institution in the making but participating in
              the making as well. They are involved in many activities apart
              from academics which makes a well-round personnel to cater service
              to the society.
            </p>
            <hr class="my-4" />

            <div className="container  shadow">
              <h1 className="display-4">Techcracy</h1>
              <div className="container">
                <div className="row">
                  <div className="col-md-3"></div>
                  <div className="col-md-6 border-top">
                    <p>
                      It is the annual technical event that has got much
                      recognition recently since its inception in 2013. It sees
                      participation from even other states of the country. The
                      major events include robotics competition, coding
                      challenge, PC Gaming, Fun Frolic events, Kokrajhar
                      Marathon, Tehno-exhibition(Jouga), Lecture Series,
                      Start-up Challenges, Cultural Evenings and many more.
                    </p>
                    <p>
                      <ViewButton
                        to={{ pathname: "https://techcracy.cit.ac.in/" }}
                        target="_blank"
                      >
                        {" "}
                        Visit Techcracy
                      </ViewButton>
                    </p>
                  </div>
                  <div className="col-md-3"></div>
                </div>
              </div>
            </div>
            <div className="container  shadow mt-5">
              <h1 className="display-4">Ecstasy</h1>
              <div className="container">
                <div className="row">
                  <div className="col-md-3"></div>
                  <div className="col-md-6 border-top">
                    <p>
                      The annual cultural fest of CIT Kokrajhar has steadily
                      grown into one of the largest youth festivals of the
                      region. It also features the Institute sports week. The
                      event has witnessed performances by some of the renowned
                      artists from across the world like: Aditi Singh Sharma,
                      Nikhil D’souza, Sunburn Campus, Zubeen Garg, GATC Band, DJ
                      Sara Santini, DJ Zypherton, DJ Nina Suerto etc.
                    </p>
                    <p>
                      <ViewButton
                        to={{ pathname: "http://ecstasy.cit.ac.in/" }}
                        target="_blank"
                      >
                        {" "}
                        Visit Ecstasy
                      </ViewButton>
                    </p>
                  </div>
                  <div className="col-md-3"></div>
                </div>
              </div>
            </div>
            <div className=" mt-5">
              <h1 className="display-4">Clubs And Activities</h1>
              <div className="container">
                <div className="row">
                  <div className="col-md-6">
                    {" "}
                    <div class="card shadow" style={{ border: "none" }}>
                      <div class="card-body">
                        <h5 class="card-title">The Astrophysics Club</h5>
                        <p class="card-text">
                          The CIT’s own astronomy club that brings closer all
                          the sky gazers to have open discussions and various
                          other activities like Movie Screening, Presentation
                          Competition, Lecture Series, School and Community
                          programs, Sky Observation sessions etc. It is headed
                          by Dr. Manasi Buzar Baruah and is managed by a Student
                          co-ordinator and other club members.
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-6 mt-2">
                    {" "}
                    <div class="card shadow" style={{ border: "none" }}>
                      <div class="card-body">
                        <h5 class="card-title">
                          Saogiri, The Photography Club
                        </h5>
                        <p class="card-text">
                          The official photography club of the institute. It
                          brings together all the like-mined explorer and
                          creative minds together. The de-facto image creation
                          of CIT Kokrajhar. And yes Thanks to them because you
                          are seeing the amazing institute digitally in our
                          website.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-6 mt-2">
                    {" "}
                    <div class="card shadow" style={{ border: "none" }}>
                      <div class="card-body">
                        <h5 class="card-title">Khobam, The Cultural Club</h5>
                        <p class="card-text">
                          Khobam is the reason all engineers are alive. Khobam
                          oraganises many dance and instrument playing sessions
                          all around the year. You will find them jamming at any
                          time @ Recreation Center.
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-6 mt-2">
                    {" "}
                    <div class="card shadow" style={{ border: "none" }}>
                      <div class="card-body">
                        <h5 class="card-title">Robotics Club</h5>
                        <p class="card-text">
                          It helps interested students to dive into the field of
                          robotics and automation. Many Club Members have
                          participated in robotics events across the country.
                          Visit –{" "}
                        </p>
                        <p>
                          <ViewButton
                            to={{ pathname: "https://roboticsclub.cit.ac.in" }}
                            target="_blank"
                          >
                            {" "}
                            Visit
                          </ViewButton>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-6 mt-2">
                    {" "}
                    <div class="card shadow" style={{ border: "none" }}>
                      <div class="card-body">
                        <h5 class="card-title">ACM-CITK Students Chapter</h5>
                        <p class="card-text">
                          This is the offcial local Student's Society of ACM{" "}
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-6 mt-2">
                    {" "}
                    <div class="card shadow" style={{ border: "none" }}>
                      <div class="card-body">
                        <h5 class="card-title">Q-ro-CITy ,The Quiz Club</h5>
                        <p class="card-text">
                          It keeps the quizzing passion alive in the institute
                          by organizing many quiz competitions throughout the
                          year
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-md-6 mt-2">
                    {" "}
                    <div class="card shadow" style={{ border: "none" }}>
                      <div class="card-body">
                        <h5 class="card-title">Ramdhenu, The Bihu Group</h5>
                        <p class="card-text">
                          It engages the students to keep alive the tradition of
                          this very land with Bihu as well as Bodo Dance. They
                          participate officially in the traditional programs in
                          the institute as well as outside and has brought
                          laurels tothe institute.
                        </p>
                      </div>
                    </div>
                  </div>
                  <div className="col-md-6 mt-2">
                    {" "}
                    <div class="card shadow" style={{ border: "none" }}>
                      <div class="card-body">
                        <h5 class="card-title">Xopun</h5>
                        <p class="card-text">
                          Xopun is the dramatics club of CIT Kokrajhar. This
                          club is created as a platform of common interests to
                          deal with stage plays, skits and short films. Here, we
                          beleive in the two-way learning process from writing
                          plays, to direction and to performance.
                        </p>

                        <p>
                          <ViewButton
                            to={{ pathname: "https://xopuncitk.github.io/" }}
                            target="_blank"
                          >
                            {" "}
                            Visit
                          </ViewButton>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Activities;
