import React from 'react'

import background from "../../img/Recruitment.jpg";
import Brochure from './Brochure';
import PastRecruiter from './PastRecruiter';
import Policy from './Policy';
import Registration from './Registration';




function RecruiterSection() {
    return (
      <>
        {" "}
        <div
          className=" text-center"
          style={{ backgroundColor: "#fff", minHeight:"86vh" }}
        >
          {" "}
          <div
            className="container-fluid text-center"
            style={{
              backgroundImage: ` linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) ),url(${background})`,
              backgroundPosition: `contain`,
              backgroundRepeat: `no-repeat`,
              backgroundSize: `100vw 100vh`,
            }}
          >
            <div
              class="jumbotron bg-transparent text-white"
              style={{ zIndex: `10` }}
            >
              <h1 class="display-1 font-weight-bold p-5 ">
                CIT Kokrajhar Recruitment Section
              </h1>
            </div>
          </div>
          <div className=" bg-white p-2" >
            <nav>
              <div
                class="nav nav-tabs justify-content-center"
                id="nav-tab"
                role="tablist"
              >
                <button
                  class="nav-link active"
                  id="nav-pastrecruiter-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#nav-pastrecruiter"
                  type="button"
                  role="tab"
                  aria-controls="nav-pastrecruiter"
                  aria-selected="true"
                >
                  Past Recruiters
                </button>

                <button
                  class="nav-link"
                  id="nav-policy-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#nav-policy"
                  type="button"
                  role="tab"
                  aria-controls="nav-policy"
                  aria-selected="false"
                >
                  Policy
                </button>
                <button
                  class="nav-link"
                  id="nav-brochure-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#nav-brochure"
                  type="button"
                  role="tab"
                  aria-controls="nav-brochure"
                  aria-selected="false"
                >
                  Brochure
                </button>
                <button
                  class="nav-link"
                  id="nav-registration-tab"
                  data-bs-toggle="tab"
                  data-bs-target="#nav-registration"
                  type="button"
                  role="tab"
                  aria-controls="nav-registration"
                  aria-selected="false"
                >
                  Registration
                </button>
              </div>
            </nav>
            <div class="tab-content" id="nav-tabContent">
              <div
                class="tab-pane fade show active"
                id="nav-pastrecruiter"
                role="tabpanel"
                aria-labelledby="nav-pastrecruiter-tab"
              >
                <PastRecruiter />
              </div>

              <div
                class="tab-pane fade"
                id="nav-policy"
                role="tabpanel"
                aria-labelledby="nav-policy-tab"
              >
                <Policy />
              </div>
              <div
                class="tab-pane fade"
                id="nav-brochure"
                role="tabpanel"
                aria-labelledby="nav-brochure-tab"
              >
                <Brochure />
              </div>
              <div
                class="tab-pane fade"
                id="nav-registration"
                role="tabpanel"
                aria-labelledby="nav-registration-tab"
              >
                <Registration />
              </div>
            </div>
          </div>
        </div>
      </>
    );
}

export default RecruiterSection
