import React, { Component } from "react";

export class SecondPart extends Component {
  render() {
    return (
      <div>
        <center>
          <h1 className="display-4">First Point of Contact</h1>
          <hr className="my-4" />
          <div className="col-md-12 col-xl-12">
            {" "}
            <label for="fpocname" class="form-label fs-3">
              Name
            </label>
            <input
              name="entry.657655039"
              type="text"
              class="form-control w-75 text-center"
              id="fpocname"
            />
          </div>
          <div className="col-md-12 col-xl-12">
            {" "}
            <label for="fpocemail" class="form-label fs-3">
              Email address
            </label>
            <input
              name="entry.2046872162"
              type="email"
              class="form-control w-75 text-center"
              id="fpocemail"
              placeholder="name@example.com"
            />
          </div>
          <div className="col-md-12 col-xl-12">
            {" "}
            <label for="fpocealtmail" class="form-label fs-3">
              {" "}
              Alternate Email address
            </label>
            <input
              name="entry.1619591619"
              type="email"
              class="form-control w-75 text-center"
              id="fpocaltemail"
              placeholder="name@example.com"
            />
          </div>
          <div className="col-md-12 col-xl-12">
            {" "}
            <label for="fpoclandline" class="form-label fs-3">
              Landline
            </label>
            <input
              name="entry.1076031809"
              type="tel"
              class="form-control w-75 text-center"
              id="fpoclandline"
            />
          </div>
          <div className="col-md-12 col-xl-12">
            {" "}
            <label for="fpocdesignation" class="form-label fs-3">
              Designation
            </label>
            <textarea
              name="entry.1991282784"
              class="form-control w-75 text-center"
              id="fpocdesignation"
              rows="2"
            ></textarea>
          </div>
          <div className="col-md-12 col-xl-12">
            {" "}
            <label for="fpocfax" class="form-label fs-3">
              Fax
            </label>
            <input
              name="entry.725234709"
              type="tel"
              class="form-control w-75 text-center"
              id="fpocfax"
            />
          </div>
        </center>
      </div>
    );
  }
}

export default SecondPart;
