import React, { Component } from "react";
import { Link } from "react-router-dom";
import { VscFilePdf } from "react-icons/vsc";

export class Brochure extends Component {
  render() {
    return (
      <>
        <div className="container pt-2">
          <Link
            to={{
              pathname:
                "https://iambhaskar21.github.io/tnp_demo/Brochure201920.pdf",
            }}
            target="_blank"
            className="display-3 text-decoration-none text-success"
          >
            <VscFilePdf size={100} />
            <br />
            Click to view our brochure
          </Link>
        </div>
      </>
    );
  }
}

export default Brochure;
