import React, { Component } from 'react'
import FirstPart from './RegistrationComponents/FirstPart';
import SecondPart from './RegistrationComponents/SecondPart';
import ThirdPart from './RegistrationComponents/ThirdPart';

export class Registration extends Component {
    render() {
        return (
          <div>
            <div className="container" style={{ marginBottom: "100px" }}>
              <h1 className="display-3">Recruiters' Registration Form</h1>

              <div className="container lead ">
                <form
                  action="https://docs.google.com/forms/u/2/d/e/1FAIpQLSdpgAZZvcD6S2ZtCOCI58hzSR0aBhacdoKKhbma6bWqjxqVLg/formResponse"
                  target="_self"
                  method="POST"
                  id="mG61Hd"
                  jsmodel="TOfxwf Q91hve"
                  data-response="%.@.[]]"
                  data-first-entry="0"
                  data-last-entry="18"
                  data-is-first-page="true"
                >
                  <div className="row shadow p-5 mb-4">
                    <FirstPart></FirstPart>
                  </div>
                  <div className="row shadow p-5 mb-4">
                    <SecondPart></SecondPart>
                  </div>
                  <div className="row shadow p-5 mb-4">
                    <ThirdPart></ThirdPart>
                  </div>
                  <center>
                    <button type="submit" class="btn btn-block btn-success">
                      Submit
                    </button>
                  </center>
                </form>
              </div>
            </div>
          </div>
        );
    }  
}

export default Registration
