import React, { Component } from "react";

export class ThirdPart extends Component {
  render() {
    return (
      <div>
        <center>
          <h1 className="display-4">Second Point of Contact</h1>
          <hr className="my-4" />
          <div className="col-md-12 col-xl-12">
            <label for="spocname" class="form-label fs-3">
              Name
            </label>
            <input
              name="entry.1822474831"
              type="text"
              class="form-control w-75 text-center"
              id="spocname"
            />
          </div>
          <div className="col-md-12 col-xl-12">
            {" "}
            <label for="spocemail" class="form-label fs-3">
              Email address
            </label>
            <input
              name="entry.1563104311"
              type="email"
              class="form-control w-75 text-center"
              id="spocemail"
              placeholder="name@example.com"
            />
          </div>
          <div className="col-md-12 col-xl-12">
            <label for="spocmobile" class="form-label fs-3">
              Mobile
            </label>
            <input
              name="entry.62030953"
              type="tel"
              class="form-control w-75 text-center"
              id="spocmobile"
            />
          </div>
          <div className="col-md-12 col-xl-12">
            {" "}
            <label for="spocdesignation" class="form-label fs-3">
              Designation
            </label>
            <textarea
              name="entry.444522708"
              class="form-control w-75 text-center"
              id="spocdesignation"
              rows="2"
            ></textarea>
          </div>
          <div className="col-md-12 col-xl-12">
            {" "}
            <label for="spocfax" class="form-label fs-3">
              Fax
            </label>
            <input
              name="entry.1663261237"
              type="tel"
              class="form-control w-75 text-center"
              id="spocfax"
            />
          </div>
        </center>
      </div>
    );
  }
}

export default ThirdPart;
